.app-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
    text-align: center;
}


.item {
    min-height: 30rem;
    min-width: 20rem;
    padding: 40px;
}
.item img {
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    pointer-events: none;
}
.inner-carousel {
    display: flex;
   /* background: lightblue; */
}
.carousel {
    cursor: grab;
    overflow: hidden;
    /* background-color: blueviolet; */
}